/* Подключение шрифта в <head> HTML-файла */
@import url('https://fonts.googleapis.com/css2?family=Baskerville:wght@700&display=swap');

.video-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    z-index: -1;
}

.fullscreen-video {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    transform: translate(-50%, -50%);
}

.coming-soon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 3rem;
    color: white;
    background-color: rgba(0, 0, 0, 0);
    padding: 2rem;
    border-radius: 10px;
    text-align: center;
    z-index: 1;
    opacity: 0;
    animation: fadeIn 2s forwards;
    font-family: 'Baskerville', serif; /* Используем выбранный шрифт */
    text-shadow: 
        1px 1px 2px rgba(255, 165, 0, 0.7), /* Лёгкая оранжевая обводка */
        -1px -1px 2px rgba(255, 165, 0, 0.7),
        1px -1px 2px rgba(255, 165, 0, 0.7),
        -1px 1px 2px rgba(255, 165, 0, 0.7); /* Добавляем контур */
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
